.todoList {
    margin-top: 10px;

    input {
        padding: 5px;
    }

    .input {
        display: flex;
        justify-content: center;
    }

    .listBody {
        display: flex;
        justify-content: space-around;
        font-size: 18px;
        .listItems {
            min-width: 260px;
            max-width: 350px;
            margin-top: 10px;
            .listItem {
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
            }
        }
    }
}